<template>
    <div>
        <Echart :options="options" id="centreLeft1Chart" height="500px" width="450px"></Echart>
    </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler(newData) {
                this.options = {
                    color: newData.color,
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b} {c}个 ({d}%)',
                    },
                    toolbox: {
                        show: true,
                    },
                    aria: {
                        enabled: true,
                        decal: {
                            show: true,
                            
                        }
                    },
                    calculable: true,
                    legend: {
                        orient: 'horizontal',
                        icon: 'circle',
                        // right: '10%',
                        // x: 'center',
                        bottom: '30',
                        data: newData.xData,
                        textStyle: {
                            color: '#fff',
                        },
                    },
                    series: [
                        {
                            name: '账户数量',
                            type: 'pie',
                            radius: ['30%', '60%'],
                            center: ['50%', '30%'],
                            avoidLabelOverlap: false,
                            itemStyle: {
                                borderRadius: 10,
                                borderColor: '#fff',
                                borderWidth: 2,
                            },
                            label: {
                                show: false,
                                position: 'center',
                            },
                            emphasis: {
                                label: {
                                    show: true,
                                    fontSize: '20',
                                    fontWeight: 'bold',
                                },
                            },
                            labelLine: {
                                show: false,
                            },
                            data: newData.seriesData,
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

