<template>
    <div class="container">
        <Chart style="border: 2px dashed #193D68;border-radius: 50%; margin-bottom: 5px;" :cdata="chartData" />
        <div>{{ chartData.title }}</div>
    </div>
</template>

<script>
import Chart from './chart.vue'
export default {
    props: {
        cdata: {
            type: Object,
            default: () => {
                return {
                    title: '人民币¥',
                    color: '#F2B845',
                    number: 500,
                    percent: 90,
                }
            },
        },
    },
    data() {
        return {
            chartData: {
                title: '人民币¥',
                color: '#F2B845',
                number: 500,
                percent: 90,
            },
            toggle: true
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.chartData = this.cdata;
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random()*5);

                let tmp1number = this.toggle ? tmp1 + this.cdata.percent : this.cdata.percent -tmp1;
                let tmp2 = Math.ceil(Math.random()*10);
                let tmp2number = this.toggle ?  tmp2 + this.cdata.number: this.cdata.number - tmp2;
                this.$set(this.chartData, 'percent', tmp1number)
                this.$set(this.chartData, 'number', tmp2number)
                // this.cdata.s3.splice(tmp3,1,tmp3number);
                console.log(this.chartData)
                this.toggle = !this.toggle;
            }, 2000)
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
</style>
