<template>
    <div>
        <Echart :options="options" id="centreLeft1Chart" height="200px" width="200px"></Echart>
    </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler(newData) {
                this.options = {
                    title: {
                        text: `${newData.percent}%\n——\n${newData.number}`,
                        x: 'center',
                        y: 'center',
                        textStyle: {
                            color: '#fff',
                            fontSize: '18',
                        },
                    },
                    tooltip: {
                        formatter: function(params) {
                            return params.name + '：' + params.percent + ' %'
                        },
                        show: true,
                    },
                    legend: {
                        show: false,
                        itemGap: 12,
                        data: ['占比', '剩余'],
                    },
                    aria: {
                        enabled: true,
                        decal: {
                            show: true,
                            decals: {
                                symbol: 'react',
                            },
                        },
                    },
                    series: [
                        {
                            name: '内环',
                            type: 'pie',
                            clockWise: true,
                            radius: ['60', '75'],
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: false,
                                    },
                                    labelLine: {
                                        show: false,
                                    },
                                },
                            },
                            hoverAnimation: false, //取消鼠标悬浮放大的效果
                            data: [
                                {
                                    value: newData.percent, //实际占比的数据
                                    name: '占比',
                                    itemStyle: {
                                        normal: {
                                            color: newData.color, //绿色区域
                                            label: {
                                                show: false,
                                            },
                                            labelLine: {
                                                show: false,
                                            },
                                        },
                                    },
                                },
                                {
                                    name: '剩余',
                                    value: 100 - newData.percent,
                                    itemStyle: {
                                        normal: {
                                            color: 'rgba(108,119,122,0.3)', //灰色区域
                                        },
                                    },
                                },
                            ],
                        },
                        {
                            name: '外环',
                            radius: ['80', '85'],
                            type: 'pie',
                            hoverAnimation: false, //取消鼠标悬浮放大的效果
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: false,
                                    },
                                    labelLine: {
                                        show: false,
                                    },
                                },
                            },
                            data: [
                                {
                                    name: '',
                                    value: 100 - newData.percent,
                                    itemStyle: {
                                        normal: {
                                            color: 'rgba(108,119,122,0.3)',
                                        },
                                    },
                                },
                            ],
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

<style lang="scss" scoped></style>
